import { NgModule, NO_ERRORS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { CostCenterMasterRoutingModule } from './cost-center-master-routing.module';
import { CostCenterMasterComponent } from './cost-center-master.component';
import { CostCenterMasterEditComponent } from './cost-center-master-edit/cost-center-master-edit.component';
import { CostCenterMasterAddComponent } from './cost-center-master-add/cost-center-master-add.component';


@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    CostCenterMasterRoutingModule,
  ],
  declarations: [
    CostCenterMasterComponent,
    CostCenterMasterEditComponent,
    CostCenterMasterAddComponent,
  ],
  entryComponents:[
    CostCenterMasterEditComponent,
    CostCenterMasterAddComponent,
  ],
  
  schemas: [ NO_ERRORS_SCHEMA ]
  
})
export class CostCenterMasterModule { }



