import { Component, OnInit,Input,Output,EventEmitter,ViewChild } from '@angular/core';
import {FormGroup,FormControl,FormBuilder,Validators } from '@angular/forms';
import {CostCenterMasterService, CostCenter,ErrorHandleService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-cost-center-master-edit',
  templateUrl: './cost-center-master-edit.component.html',
  styleUrls: ['./cost-center-master-edit.component.scss']
})
export class CostCenterMasterEditComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @Input('CostCenterEditData') CostCenterEditData;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  EditData:any;
  costCenterForm:FormGroup;
  costCenterData:CostCenter;

    constructor( private fb:FormBuilder,
      private ehs: ErrorHandleService,
      private ccm: CostCenterMasterService) {
      this.createForm();
     
     }
  
    ngOnInit() {
        this.costCenterData=new CostCenter(this.CostCenterEditData);
        this.costCenterForm.setValue(this.costCenterData);
    }
    createForm() {
  
      this.costCenterForm = this.fb.group({ 
        'code': [null,Validators.required],
        'name': [null,Validators.required],
        'remarks': [null,Validators.required]
      });
    }
    saveBtn(post){ 
      this.jqxLoader.open();
      this.ccm.update(this.CostCenterEditData.id,post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.UpdatedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
      });
     }
     cancelBtn(){
      this.cancelData.emit(true);
     }
     rules =
     [
         { input: '.code', message: ' Code is required !', action: 'keyup, blur', rule: 'required' },
         { input: '.name', message: 'Name is required!', action: 'keyup, blur', rule: 'required' },
         { input: '.remarks', message: 'Remark is required!', action: 'keyup, blur', rule: 'required' },
         
         
         
     ];     
       

}



